.colorPallete {
  max-width: 600px;
  display: flex;

}

.color-item {
  display: block;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: 1px solid;
  margin: 0 0.3rem;
}
.dynamic-color-item-space {
  display: flex;
  flex-direction: row-reverse;
}

.custom-color-item {
  display: block;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: 1px solid;
  margin: 0 0.3rem;
}

i {
  color: gray;
  margin-right: -1;
}
.customColorPallete {
  display: flex;
  max-width: 600px;

}
form button {
  margin-left: 10px;
  background: skyblue;
  padding: 5px;
  color: black;
  border-radius: 300px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
}
.header {
  height: 80px;
  box-shadow: 0px 4px 5px rgba(0,0,0, 0.4);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;

}

#plus-icon:hover{
  cursor: pointer;
}

#plus-icon {
  margin-right: 10px;
}
.drawer {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

h2 {
  width: 200px;
  margin: 0 auto;  
}
div.wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  height: 100vh;
  flex-direction: column;
}
